// ""
<template>
  <div class="banner">
    <swiper
      :initial-slide="2"
      :slides-per-view="2"
      :centered-slides="true"
      :space-between="0"
      :navigation="true"
      :loop="true"
      :pagination="{ clickable: true }"
      :lazy="{ loadPrevNext: true }"
      :autoplay="{ delay: 4000, disableOnInteraction: false }"
    >
      <swiper-slide v-for="(banner, index) in banners" :key="index" @click="clickSlide(index)">
        <img :data-src="genImgURL(banner.imageUrl, 1000, 400)" class="swiper-lazy" />
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
import SwiperCore, { Navigation, Pagination, Autoplay, Lazy } from "swiper"
import { Swiper, SwiperSlide } from "swiper/vue/swiper-vue"
import "swiper/swiper-bundle.css"
import { reactive, nextTick } from "vue"
import { reqBanners } from "@/api/discovery"
import { genImgURL } from "@/utils/common"

SwiperCore.use([Navigation, Pagination, Autoplay, Lazy])
export default {
  setup() {
    // swiper7 <swiper :options="swiperOptions"> 
    //#region
    // const swiperOptions = reactive({
    //   slidesPerView: 2,
    //   centeredSlides: true,
    //   spaceBetween: 0,
    //   navigation: true,
    //   loop: true,
    //   speed: 500,
    //   pagination: {
    //     clickable: true,
    //   },
    //   autoplay: {
    //     delay: 3000,
    //     disableOnInteraction: false,
    //   },
    // })
    //#endregion

    let banners = reactive([])
    ;(async function getBanner() {
      let result = await reqBanners()
      if (result.code === 200) {
        banners = Object.assign(banners, result.banners)
      }

      nextTick(_initSwiper)
    })()

    function _initSwiper() {
      /*
      : .swiper-slide-prev,1.
            .swiper-button-prev,, 1
            :hover,,:hover.
    */
      let sideImgs = []
      sideImgs[0] = document.querySelector(".swiper-slide-next")
      sideImgs[1] = document.querySelector(".swiper-slide-prev")
      let btns = []
      btns[0] = document.querySelector(".swiper-button-next")
      btns[1] = document.querySelector(".swiper-button-prev")
      // 
      if (sideImgs[0] && sideImgs[1]) {
        // ,on-hover; ,on-hover
        sideImgs.forEach((img) => {
          img.onmouseenter = () => {
            img.classList.add("on-hover")
          }
          img.onmouseleave = () => {
            img.classList.remove("on-hover")
          }
        })
        // ,on-hover
        btns[0].onmouseenter = () => {
          sideImgs[0].classList.add("on-hover")
        }
        btns[1].onmouseenter = () => {
          sideImgs[1].classList.add("on-hover")
        }
      }

      //, 
      const swiper = document.querySelector(".swiper")
      swiper.onmouseenter = () => {
        btns.forEach((btn) => {
          btn.style.setProperty("opacity", "1")
        })
      }
      swiper.onmouseleave = () => {
        btns.forEach((btn) => {
          btn.style.setProperty("opacity", "0")
        })
      }

      // ,click,
      let paginations = document.querySelectorAll(".swiper-pagination-bullet")
      paginations.forEach((pagination) => {
        pagination.onmouseenter = pagination.click
      })

      /*
        ,transition
        transitioncss,,,
          transition,,
        100mstransition,,
      */
      setTimeout(() => {
        let swiperSlides = document.querySelectorAll(".swiper-slide")
        swiperSlides.forEach((swiperSlide) => {
          swiperSlide.style.setProperty("transition", "0.4s")
        })
      }, 100)
    }

    // : slide, ,.
    // :.,,,url
    function clickSlide(index) {
      // slide
      const slide = window.event.target.parentNode
      // slideslide,
      if (slide.classList.contains("swiper-slide-active")) {
        // url
        if (banners[index].url) {
          window.open(banners[index].url)
        }
      }
      // ,.
      // swiperslideToClickedSlide. ,,swiper-slide-active,
      else {
        // ,swiper. 
        if (slide.classList.contains("swiper-slide-prev")) {
          const leftBtn = document.querySelector(".swiper-button-prev")
          leftBtn.click()
        } else {
          const rightBtn = document.querySelector(".swiper-button-next")
          rightBtn.click()
        }
      }
    }

    return { banners, clickSlide, genImgURL }
  },
  components: {
    Swiper,
    SwiperSlide,
  },
}
</script>

<style lang="less">
.banner {
  position: relative;
  width: 100%;
  // ,swiper
  max-width: 936px;
  max-height: 230px;
  margin: 0 auto;
  .swiper {
    padding-top: 10px;
    padding-bottom: 30px;
    .swiper-slide {
      text-align: center;
      font-size: 18px;
      background: #fff;
      overflow: hidden;

      /* Center slide text vertically */
      display: -webkit-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      -webkit-justify-content: center;
      justify-content: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      -webkit-align-items: center;
      align-items: center;
      // transition,setup()
      // transition: 0.3s;
      opacity: 0.7;
      &.on-hover {
        opacity: 1;
      }
      img {
        border-radius: 5px;
        width: 100%;
        /*
            
          */
        max-width: 500px;
        max-height: 200px;
      }
    }
    .swiper-slide-next {
      transform: translateX(-45%) scale(0.9);
    }
    .swiper-slide-prev {
      transform: translateX(45%) scale(0.9);
    }
    .swiper-slide-active,
    .swiper-slide-duplicate-active {
      transform: scale(1.05);
      z-index: 800;
      opacity: 1;
    }
    .swiper-button-prev,
    .swiper-button-next {
      position: absolute;
      top: 40%;
      width: 36px;
      height: $width;
      border-radius: 50%;
      background-color: rgba(0, 0, 0, 0.1);
      color: rgba(255, 255, 255, 0.9);
      transition: 0.3s;
      // ,
      opacity: 0;
      &:hover {
        background-color: rgba(0, 0, 0, 0.3);
      }
    }
    .swiper-button-prev::after,
    .swiper-button-next::after {
      font-size: 10px;
    }
    .swiper-pagination {
      position: absolute;
      bottom: 0px;
      .swiper-pagination-bullet {
        box-sizing: content-box;
        // padding
        background-clip: content-box;
        width: 30px;
        height: 2px;
        padding: 10px 0;
        margin: 0 5px;
        border: none;
        border-radius: 0;
        background-color: #c0c4cc;
        .swiper-pagination-bullet-active {
          background-color: #c0c4cc;
        }
      }
    }
  }
}
</style>
